.header {
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  padding: 30px 0;
  color: $color-alice-blue;
  z-index: 1;

  @include vp-1023 {
    padding: 20px 0;
  }

  @include vp-767 {
    padding: 15px 0;
  }
}

.header__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1222px;
  margin: 0 auto;

  @include vp-1023 {
    padding: 0 78px 0 50px;
  }

  @include vp-767 {
    padding: 0;
    padding-right: 18px;
  }
}

.header__contacts {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 10px;
  width: 38%;
  margin-top: 10px;
  margin-left: 8px;

  @include vp-1023 {
    width: auto;
    gap: 40px;
  }

  @include vp-767 {
    margin-top: 5px;
  }
}

.header__contact-link {
  display: flex;
  gap: 15px;
  align-items: center;
  color: $color-alice-blue;
}

.header__contact-link .description {
  font-size: 16px;
  line-height: 18px;

  @include vp-1023 {
    display: none;
  }
}

.header__button {
  margin-right: 60px;
  margin-bottom: 4px;

  @include vp-1023 {
    display: none;
  }
}
