html {
  min-width: 320px;
  scroll-behavior: smooth;
  margin: 0 auto;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));

  .header,
  .footer {
    flex-shrink: 0;
  }

  main {
    flex-grow: 1;
  }
}

.rouble {
  font-family: $ff-rouble;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;

  white-space: nowrap;

  border: 0;

  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;

  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;

  &::-webkit-scrollbar {
    display: none;
  }
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.title,
.description {
  margin: 0;
}

.title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
}

.description {
  font-family: "PTMono", "Courier New", monospace;
}

.list {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.input::placeholder {
  color: $color-alice-blue;
}

.input {
  font-family: "PTMono", "Courier New", monospace;
  color: $color-alice-blue;
  border: none;
  border-radius: 4px;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 16px;
  cursor: pointer;
}
