.question-form__label--question {
  grid-column: 1/-1;
  margin-top: 8px;
}

.question-form input {
  width: 100%;
  font-family: "PTMono", "Courier New", monospace;
  color: #f2f5f6;
  border: none;
  border-radius: 4px;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 16px;
  cursor: pointer;
}

.question-form input::placeholder {
  color: #f2f5f6;
}

.question-form input[type=checkbox] {
  width: auto;
}

.question-form textarea {
  width: 100%;
  margin-top: 10px;
  font-family: "PTMono", "Courier New", monospace;
  color: #f2f5f6;
  border: none;
  border-radius: 4px;
  background-color: rgba(4, 20, 39, 0.15);
  padding: 26px 16px 20px;
  cursor: pointer;
}
@media (max-width: 1023px) {
  .question-form textarea {
    padding-top: 32px;
    padding-bottom: 12px;
  }
}
@media (max-width: 767px) {
  .question-form textarea {
    padding-top: 24px;
    padding-bottom: 45px;
  }
}

.question-form textarea::placeholder {
  color: #f2f5f6;
}

.question-form button {
  grid-column: 1/2;
  margin-top: 29px;
  margin-left: 1px;
  margin-right: 9px;
}
@media (max-width: 1023px) {
  .question-form button {
    margin: 30px 55px 2px 0;
  }
}
@media (max-width: 767px) {
  .question-form button {
    margin: 0;
    margin-top: 25px;
  }
}

.question-form__personal-data {
  grid-column: 2/-1;
  display: flex;
  gap: 11px;
  align-items: center;
  margin-top: 43px;
  margin-left: 11px;
}
@media (max-width: 1023px) {
  .question-form__personal-data {
    margin: 0;
    margin-top: 32px;
  }
}
@media (max-width: 767px) {
  .question-form__personal-data {
    grid-column: 1/2;
    margin-top: 24px;
    margin-left: 7px;
  }
}

.question-form__custom-checkbox {
  position: relative;
  box-sizing: border-box;
  width: 20px;
  height: 15px;
  border: 1px solid #ffffff;
  border-radius: 2px;
}

.question-form input:checked ~ .question-form__custom-checkbox::before {
  content: "";
  position: absolute;
  width: 3px;
  height: 10px;
  top: 0;
  left: 40%;
  border-right: 1px solid #ffffff;
  border-bottom: 1px solid #ffffff;
  transform: rotate(45deg);
}

.question-form__personal-description {
  font-size: 14px;
  line-height: 16px;
}

.footer {
  background-color: #041827;
  padding: 50px 0;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .footer {
    padding-top: 50px;
    padding-bottom: 64px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 34px 0;
  }
}

.footer__container {
  display: flex;
  justify-content: space-between;
  max-width: 1222px;
}
@media (max-width: 1023px) {
  .footer__container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 34px;
  }
}
@media (max-width: 767px) {
  .footer__container {
    grid-template-columns: 1fr;
    gap: 0;
    padding: 0;
  }
}

@media (max-width: 1023px) {
  .footer__wrapper {
    grid-column: 1/-1;
    display: flex;
    justify-content: space-between;
    padding-left: 50px;
    padding-right: 49px;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
    justify-self: start;
    padding: 0;
    padding-left: 50px;
  }
}

.footer__social-list {
  display: flex;
  flex-wrap: wrap;
  gap: 26px;
  margin-top: 35px;
  margin-left: 14px;
}
@media (max-width: 1023px) {
  .footer__social-list {
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .footer__social-list {
    justify-content: center;
    margin-top: 20px;
    margin-left: 20px;
    gap: 25px;
  }
}

.footer__nav {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 7px;
  margin-left: 46px;
}
@media (max-width: 1023px) {
  .footer__nav {
    margin-top: 3px;
    margin-left: 50px;
  }
}
@media (max-width: 767px) {
  .footer__nav {
    gap: 0;
    margin: 0;
    margin-top: 23px;
    border-top: 1px solid #f2f5f6;
    border-bottom: 1px solid #f2f5f6;
  }
}

.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 7px;
  margin-right: 38px;
}
@media (max-width: 1023px) {
  .footer__contacts {
    margin-top: 3px;
  }
}
@media (max-width: 767px) {
  .footer__contacts {
    gap: 0;
    margin: 0;
    border-bottom: 1px solid #f2f5f6;
  }
}

.nav__title {
  font-size: 15px;
  line-height: 16px;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .nav__title {
    position: relative;
    padding: 23px 0 20px 24px;
  }
}

@media (max-width: 767px) {
  .nav__title::before {
    content: "";
    position: absolute;
    width: 15px;
    height: 1px;
    background-color: #f2f5f6;
    top: 28px;
    right: 26px;
  }
}

.nav__title--closed::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 1px;
  background-color: #f2f5f6;
  top: 28px;
  right: 26px;
  transform: rotate(90deg);
}

.nav__wrapper {
  display: flex;
  gap: 48px;
}
@media (max-width: 1023px) {
  .nav__wrapper {
    flex-direction: column;
    gap: 4px;
  }
}
@media (max-width: 767px) {
  .nav__wrapper {
    transition: 0.2s;
    max-height: 0;
    overflow: hidden;
    padding-left: 24px;
    gap: 10px;
  }
}

.nav__list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.nav__link {
  font-family: "PTMono", "Courier New", monospace;
  font-style: normal;
  color: rgba(255, 255, 255, 0.7);
  flex-grow: 0;
}
@media (max-width: 767px) {
  .nav__link {
    font-size: 14px;
    line-height: 18px;
    padding-bottom: 10px;
  }
}

.pop-up {
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  width: 100vw;
  height: 100%;
  background-color: rgba(4, 24, 39, 0.7);
  transition: 0.5s;
  z-index: -10;
}
@media (max-width: 1023px) {
  .pop-up {
    display: none;
  }
}

.pop-up__wrapper {
  position: relative;
  display: block;
  top: 50%;
  left: 50%;
  width: 500px;
  background-color: #00b2ff;
  padding: 40px;
  border-radius: 4px;
  transform: translate(-50%, -50%);
}

.pop-up__title {
  color: #ffffff;
}

.pop-up__description {
  color: #ffffff;
  margin-top: 15px;
}

.pop-up__button {
  position: absolute;
  top: 15px;
  right: 15px;
}

.pop-up form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 40px;
}

.pop-up .question-form__label--question {
  margin-top: 0;
}

.pop-up form button {
  margin-top: 5px;
}

.pop-up .question-form__personal-data {
  margin-top: 0;
  color: #ffffff;
}

